.myModal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  color: black;
}

.myModalContent {
  padding: 25px;
  background-color: white;
  border-radius: 16px;
  min-width: 250px;
  width: 75%;
}

.myModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

.myModal button {
  border-color: black;
  color: black;
}